
import XHeader from 'vux/src/components/x-header/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'

export default {
name: 'fundPortfolio',
data() {
return {
title: {
title1: "基金名称",
title2: "单位净值",
title3: "日涨跌幅",
title4: "购买"
},
fundInfo: [],
fundType: '1000',//基金类型
sectorList: [],
upTime:''
}
},
computed: {

},
components: {
XHeader,
Tab,
TabItem,
Flexbox,
FlexboxItem
},
mounted() {
this.getfundInfo('1000');
this.querySector();
},
methods: {
colorType(e) {
if (e >= 0) {
return "_ff5255"
} else {
return "_36cca4"
}
},
goSearch() {
this.$router.push('/fundPortfolio/search');
},
onItemClick(index) {
document.querySelectorAll('#fundInfo')[0].scrollTop = 0;
if (index == 3) {
this.title.title2 = "万份收益";
this.title.title3 = "七日年化";
} else {
this.title.title2 = "单位净值";
this.title.title3 = "日涨跌幅";
}
switch (index) {
case 0:
this.fundType = "1000";
break;
case 1:
this.fundType = "1001";
break;
case 2:
this.fundType = "1002";
break;
case 3:
this.fundType = "1005";
break;
case 4:
this.fundType = "1003";
break;
case 5:
this.fundType = "1004";
break;
}
this.getfundInfo(this.fundType);
},
getfundInfo(fundType) {

let _this = this;
this.post({
url: "/fundMsg/queryFundMsg/v1.0",
data: {
fundType: fundType
},
success: function(e) {
if (e.code == "0000") {
_this.fundInfo = e.result.list;
_this.upTime=e.result.list[0].updateDate;
}
}
})
},
querySector() {
let _this = this;
this.post({
url: "/fundMsg/querySector/v1.0",
success: function(e) {
if (e.code == "0000") {
_this.sectorList = e.result.sectorList;
}
}
})
}
}
}



